  <svg class="rectangle__timer"
       ref="newDigits"
       width="145" height="10"
       viewBox="0 0 145 10" fill="none"
       xmlns="http://www.w3.org/2000/svg">
  </svg>


</template>

<script>
export default {
  data() {
    return {
      path: []
    }
  },
  computed: {
    digitsArray() {
      return Array.from(this.path);
    },
  },
  methods: {
  },
  mounted() {
    const number = '2314742400'.split('')
    const digits = this.$refs.digits;
    const digitList = Array.from(digits.querySelectorAll('path'))
    // const newSvg = this.$refs.newDigits;

    number.map(item => {
      digitList.forEach(digit => {
        if (item === digit.id) {
          this.path.push(digit);
          // newSvg.appendChild(digit);
          // console.log(digit)
        }
      })
    })
    // console.log(this.digitsArray);
  }
}
</script>